html {
  font-size: 18px;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400px;
  color: #000;
  background-color: #fff;
  min-width: 320px;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  padding: 50px 0;
}

a {
  color: #111;
}

a:hover {
  color: #555;
}

/* Header */
.navbar-style .navbar-nav .nav-link {
  color: #111;
  font-weight: 500;
  text-transform: uppercase;
}

.navbar-style .navbar-nav .nav-link i::before {
  font-size: 24px;
}
.navbar-style .dropdown-menu-fix {
  right: 0;
  left: auto;
}

.top-menu li a::after {
  content: "";
  display: block;
  width: 100%;
  background-color: #111;
  height: 1px;
  transition: all 0.3s;
  transform: scale(0);
}
.top-menu li a:hover::after {
  transform: scale(1);
}
.modal-body img {
  max-width: 100px;
}
/* Header */

/*Carousel*/
.shop-carousel {
  padding: 0;
}

.shop-carousel .carousel-item {
  display: flex;
  max-height: 80vh;
}

.shop-carousel .carousel-item img {
  object-fit: cover;
  object-position: center;
}

/*Carousel*/

/*Cards*/

.product-card {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.06);
  transition: all 0.3s;
}
.product-card:hover {
  box-shadow: 0 14px 30px -15px rgba(0, 0, 0, 0.75);
}

.product-thumb a {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  padding: 20px;
}

.product-thumb img {
  max-width: 100%;
  max-height: 100%;
}

.product-details {
  padding: 20px;
}

.product-details h4 a {
  font-weight: 500;
  display: block;
  height: 60px;
  text-decoration: none;
  overflow: hidden;
}

.product-details p {
  font-size: 15px;
  margin-bottom: 20px;
  color: #999;
  height: 44px;
  overflow: hidden;
}

.product-bottom-details {
  overflow: hidden;
  border-top: 1px solid #eee;
  padding-top: 20px;
}

.product-price {
  font-size: 18px;
  color: #ee6e73;
  font-weight: 600;
}

.product-price small {
  color: #ccc;
  font-weight: 400;
  text-decoration: line-through;
  margin-right: 5px;
}

.product-links a {
  font-size: 20px;
  margin-left: 10px;
}

/*Cards*/

/*Footer*/

.footer {
  background-color: #343443;
}

.footer h4 {
  color: #ccc;
  text-transform: uppercase;
  font-size: 22px;
  border-bottom: 1px solid #666;
  padding-bottom: 3px;
}

.footer a {
  text-decoration: none;
  transition: all 0.3s;
}

.footer a:hover {
  color: #fff;
  text-decoration: underline;
}

.footer a,
.footer li {
  color: #c8c8c8;
}

.footer li a {
  display: block;
}

.footer-icons a {
  display: inline-block;
  margin-right: 15px;
  font-size: 22px;
}

/*Footer*/
